.container{
    width: 80%;
    margin: auto;
}

label{
    display: block;
    font-size: 18px;
    font-weight: bold;
    margin-top: 10px;
}

input{
    width: 100%;
    font-size: 18px;
    padding: 5px 8px;
    border-radius: 8px;
    border:none;
    outline:none;
    box-shadow: 0 0 12px 0px grey;
    margin-top: 5px;
}

.submitBtn{
    background-color: rgb(114, 114, 114);
    color: #fff;
    font-size: 22px;
    width: 20%;
    padding: 3px 22px;
    margin-top: 10px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0 0 15px 5px rgb(255, 255, 255);
}
.submitBtn:hover{
    background-color: rgb(160, 160, 160)
}
.submitBtn:active{
    transform: scale(0.9);
    background-color: rgb(91, 91, 91)
}