* {
  margin: 0;
  padding: 0;
}

.myCard {
  background-color: #fff;
  width: 280px;
  height: 300px;
  padding: 10px;
  margin: 10px;
  position: relative;
  border-radius: 15px;
  box-shadow: 0 0 20px rgb(80, 80, 80);
  overflow: hidden;
}

.discountCart {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.discount {
  font-size: 14px;
  padding: 3px 10px;
  background-color: rgb(239, 9, 9);
  color: #fff;
  border-radius: 10px;
}
.myCart {
  font-size: 12px;
  box-shadow: 0 0 10px grey;
  border-radius: 50px;
  padding: 5px;
  cursor: pointer;
}
.myCart:active {
  background-color: rgb(145, 57, 22);
  transform: rotate(180 deg);
}

.productThumbnail {
  width: 108%;
  height: 60%;
  position: relative;
  left: -10px;
  margin-top: 10px;
}

.titlePrice {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.title {
  font-size: 20px;
  width: 80%;
}
.price {
  font-weight: bold;
  color: red;
}

.ratingBuyBtn {
  position: relative;
}
.rating {
  position: absolute;
  left: 0px;
  top: 25px;
}
.buyBtn {
  position: absolute;
  right: 0px;
  top: 25px;
  background-color: rgb(251, 120, 69);
  border: none;
  padding: 3px 10px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  border-radius: 6px;
  cursor: pointer;
}
.buyBtn:active {
  background-color: rgb(145, 57, 22);
}
